.button {
  width: 170px;
  height: 50px;
  
  font-family: "Raleway";
  color: #ffffff;
  font-size: 14px;
  font-weight: 800;
  text-transform: uppercase;

  box-shadow: inset 0 -3px 0 #736357;
  border-radius: 3px;
  background-color: #998675;
  border: none;

  &:hover {
    box-shadow: inset 0 -3px 0 #998675;
    background-color: #736357;
  }
}